import React from 'react';
import { Link } from 'react-router-dom';
import propTypes from 'prop-types';
import './Movie.css';

function Movie({ id, title, year, rating, genres, summary, thumbnail }) {
    return(
        <div className="movie">
            <Link to={{
                pathname: `/movie-detail/${id}`,
                state: {
                    title,
                    year,
                    rating,
                    genres,
                    summary,
                    thumbnail
                }
            }}>
                <img src={thumbnail} alt={title} className="movie__image" />
                <div className="movie__data">
                    <h2 className="movie__title">{title}</h2>
                    <p>
                        <span className="movie__year">{ year }</span>&nbsp;/&nbsp; 
                        <span className="movie__rating">{ rating }</span>
                    </p>
                    <ul className="movie_genres">
                        {genres.map((genre, index) => (
                            <li key={index} className="genres__genre">{genre}</li>
                        ))}
                    </ul>
                    <p className="movie__summary">{summary.slice(0, 180)}...</p>
                </div>
            </Link>
        </div>
    );
}

Movie.propTypes = {
    id: propTypes.number.isRequired,
    title: propTypes.string.isRequired,
    year: propTypes.number.isRequired,
    rating: propTypes.number.isRequired,
    genres: propTypes.arrayOf(propTypes.string).isRequired,
    summary: propTypes.string.isRequired,
    thumbnail: propTypes.string.isRequired
}

export default Movie;