import React from 'react';
import axios from 'axios';
import SearchMovie from '../components/SearchMovie';
import './Home.css';
import './Search.css';

class Search extends React.Component {
    state = {
        isLoading: true,
        movies: [],
        value: ''
    }

    getSearchMovie = async () => {
        const CLIENT_ID = 'SuFbiwqG0yVUW5ScOvUz';
        const SECRET_KEY = 'DXWSPLfH9Z';
        const search = this.state.value;
        try {
            if(search === '') {
                this.setState({
                    isLoading: false,
                    movies: []
                });
            }else {
                // const data = await searchApi.search(search);
                const { data: {items}} = await axios.get('/api/v1/search/movie.json', {
                    params: {
                        query: search,
                        display: 10
                    },
                    headers: {
                        'X-Naver-Client-Id':CLIENT_ID,
                        'X-Naver-Client-Secret':SECRET_KEY,
                        'Access-Control-Allow-Origin':'*'
                    }
                });
                console.log(items);
                const value = '';
                this.setState({movies:items, isLoading:false, value});
            }
        } catch (error) {
            console.log(error);
        }
        
    }

    handleSubmit = (e) => {
        e.preventDefault();
        this.getSearchMovie();
    }

    handleChange = (e) => {
        e.preventDefault();
        this.setState({value: e.target.value});
    }

    componentDidMount() {
        this.getSearchMovie();
    }

    render() {
        const {isLoading, movies, value} = this.state;
        return(
            <section className="search section">
                <form className="search__form" onSubmit={this.handleSubmit}>
                    <input type="text" value={value} placeholder="영화 검색" className="search__input" onChange={this.handleChange} />
                    <div className="movies">
                        { isLoading ? (
                            <div className="loader">
                                <span className="loader__text">Loading...</span>
                            </div>  
                        ) : (
                            movies.map(movie => (
                                <SearchMovie  
                                    key={movie.link.slice(-6)}
                                    id={movie.link.slice(-6)}
                                    link={movie.link}
                                    title={movie.title}
                                    subtitle={movie.subtitle}
                                    year={movie.pubDate}
                                    rating={movie.userRating}
                                    director={movie.director}
                                    actor={movie.actor}
                                    thumbnail={movie.image}
                                />
                            ))
                        )}
                    </div>
                </form>
            </section>
        );
    }
}

export default Search;