import React from 'react';
import propTypes from 'prop-types';
import './SearchMovie.css';

function SearchMovie({ id, link, title, subtitle, year, rating, director, actor, thumbnail }) {
    return(
        <div className="search-movie">
            <a href={link} target="_blank" rel="noreferrer">
                <img src={thumbnail} alt={title} className="search-movie__image" />
                <div className="search-movie__data">
                    <h2 className="search-movie__title">
                        {title.replace(/<b>/gi, "").replace(/<\/b>/gi, "")}
                        <span className="search-movie__subtitle">({subtitle})</span>
                    </h2>
                    <p>
                        <span className="search-movie__year">{ year }</span>&nbsp;/&nbsp; 
                        <span className="search-movie__rating">{ rating }</span>
                    </p>
                    <p className="search-movie__director">{director}</p>
                    <p className="search-movie__actor">{actor.slice(0,140)}...</p>
                </div>
            </a>
        </div>
    );
}

SearchMovie.propTypes = {
    id: propTypes.string.isRequired,
    link: propTypes.string.isRequired,
    title: propTypes.string.isRequired,
    subtitle: propTypes.string.isRequired,
    year: propTypes.string.isRequired,
    rating: propTypes.string.isRequired,
    director: propTypes.string.isRequired,
    actor: propTypes.string.isRequired,
    thumbnail: propTypes.string.isRequired
}

export default SearchMovie;